import { useWindowDimensions } from 'react-native';


/////////////////
export function Fluid({ min, max }) {
  const { width } = useWindowDimensions();
  const responsiveFontSize = Math.min(Math.max(width * 0.05, min), max);
  return responsiveFontSize;
};

///////////////////
export function Scale(Mobile, Tablet, Desktop) {
  const { width } = useWindowDimensions();

  if (width <= 640) {
    return Mobile;
  } else if (width <= 1008) {
    return Tablet;
  } else {
    return Desktop;
  }
}

///////////////
export function vh(percentage) {
  const windowHeight = useWindowDimensions().height;
  return (windowHeight * percentage) / 100;
}

export function vw(percentage) {
  const windowWidth = useWindowDimensions().width;
  return (windowWidth * percentage) / 100;
}

export function vmin(percentage) {
  const windowWidth = useWindowDimensions().width;
  const windowHeight = useWindowDimensions().height;
  const windowMin = Math.min(windowWidth, windowHeight);
  return (windowMin * percentage) / 100;
}

export function vmax(percentage) {
  const windowWidth = useWindowDimensions().width;
  const windowHeight = useWindowDimensions().height;
  const windowMax = Math.max(windowWidth, windowHeight);
  return (windowMax * percentage) / 100;
}

//////////////////

